.carousel-item img {
  filter: brightness(50%);
}
.carousel-indicators li {
  height: 5px;
}

.card-img {
  width: 25vw;
}
i:hover {
  cursor: pointer;
}
.auth {
  color: #28a745;
}
.auth:hover {
  color: #63c97b;
}
.address {
  color: white;
  float: right;
  font-size: 1.2vw;
  margin: 0;
  padding: 0;
}

.address span {
  font-size: 1.2vw;
  font-weight: 400;
  text-align: right;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.login span {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 400;
}
.auth span {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 400;
}
.login:hover,
.auth1:hover {
  color: rgb(199, 199, 199);
}

div.col-6.card {
  background-color: #343a40;
  justify-content: center;
  border: none;
  border-radius: 0;
}
.contain {
  background-color: #343a40;
}

img.col-6 {
  padding-right: 0;
  height: 80%;
  display: block;
  margin: auto;
}

#services_card {
  flex-direction: row;
  margin-bottom: 1rem;
}

#cert-btn {
  padding-left: 2rem;
  padding-right: 2rem;
}
#alert {
  position: fixed;
  z-index: 10;
  left: 10vw;
  right: 10vw;
  top: 5vh;
  transition: 1s ease;
  text-align: center;
}

.find:hover {
  color: lightgray;
}
.find span {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 400;
}
@media screen and (max-width: 678px) {
  #services_card {
    flex-direction: column;
    width: 21rem;
  }

  #cert-btn,
  #cert-input {
    font-size: 80%;
    padding: 0;
    line-height: 1.5rem;
    height: 1.5rem;
    margin: 0.5rem 0;
  }
  #cert-btn {
    padding: 0 0.6rem;
  }
  .carousel-text {
    font-size: 60%;
  }
}
